}

header {
    min-height: 150px;
}

.containerz {
    background-color: #050A30;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    height: calc(100vh - (150px));
    flex-wrap: wrap;
    overflow: scroll;
}

.header-profilez h1 {
    font-family: Ubuntu;
    font-style: normal;
    font-weight: 500;
    font-size: 3rem;
    color: #F4F4F4;

}

.profile-containerz {
    display: flex;
    justify-content: center;
    padding: 3rem;
    flex-direction: column;
    grid-gap: 2rem;
    gap: 2rem;
    max-width: 100%;
    width: 100%;
}

.profile-sidebarsz {
    display: flex;
    max-width: 100%;
    grid-gap: 10rem;
    gap: 10rem;
    justify-content: flex-start;
    flex: 1 1 auto
}

.left-sidebarz {
    display: flex;
    flex-direction: column;
    max-width: 35%;
    justify-content: flex-start;
    grid-gap: 4rem;
    gap: 4rem;
}

.left-sidebar-1z {
    display: flex;
    flex-direction: column;
    max-width: 100%;
    background-color: #11163A;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.16);
    border-radius: 6px;
    justify-content: flex-start;
    grid-gap: 4rem;
    gap: 4rem;
    padding: 2rem;
}

.left-upz {
    display: flex;
    flex-direction: row;
    flex-basis: 20%;
    justify-content: flex-start;
    grid-gap: 2rem;
    gap: 2rem;
    max-width: 100%;
}

.switchz {
    position: relative;
    display: inline-block;
    width: 3.5rem;
    height: 2.2rem;
}

.sliderz {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #C4C4C4;
    transition: .4s;
    border-radius: 34px;
    margin-left: -1rem;
}

.sliderz input {
    display: none;
}

.sliderz:before {
    position: absolute;
    content: "";
    height: 1.5rem;
    width: 1.5rem;
    left: 0.5rem;
    bottom: 0.3rem;
    background-color: white;
    transition: 0.4s;
    border-radius: 5rem;
}

input:checked+.sliderz {
    background-color: #050A30;
}

input:checked+.sliderz:before {
    transform: translateX(2rem);
}

.left-sidebar-iconsz {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    grid-gap: 5rem;
    gap: 5rem;
    max-width: 15%;
    cursor: pointer;
}

.left-sidebar-iconsz img {
    width: 2rem;
}

.left-sidebar-contentz {
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
    grid-gap: 5rem;
    gap: 5rem;
    flex-direction: column;
    max-width: 70%;
}

.left-sidebar-togglez {
