.dropdown {
    position: relative;
}

.dropdown:hover .dropdown-list {
    opacity: 1;
    visibility: visible;
}

.dropdown-select {
    padding: 1.2rem;
    border-radius: 4px;
    background-color: white;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 1.6rem;
    cursor: pointer;
    font-family: Ubuntu;
    font-style: normal;
    font-weight: normal;
}

.dropdown-list {
    border-radius: 4px;
    background-color: white;
    position: absolute;
    top: 70%;
    left: 0;
    right: 0;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.2s linear, visibility 0.2s linear;
    transform: translateY(10px);
    z-index: 999;
}

.dropdown-list:before {
    content: "";
    width: 100%;
    height: 10px;
    background-color: transparent;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateY(-100%);
}

.dropdown-list__item {
    padding: 1rem;
    font-size: 1.6rem;
    cursor: pointer;
    font-family: Ubuntu;
    font-style: normal;
    font-weight: normal;
}

.dropdown-list__item:hover {
    background-color: #9B9B9B;
}

