    font-style: normal;
    font-weight: normal;
    font-size: 2rem;
    color: #F4F4F4;
}

.Tracking-Left-content1 h2 {
    font-family: Ubuntu;
    font-style: normal;
    font-weight: 500;
    font-size: 3rem;
    color: #FFFFFF;
}

.Tracking-Left-content1 span {
    font-family: Ubuntu;
    font-style: normal;
    font-weight: 500;
    font-size: 1.4rem;
    color: #F4F4F4;
}

.Order-2 {
    display: flex;
    max-width: 100%;
    width: 100%;
}

.Order-3 {
    display: flex;
    max-width: 25%;
    width: 100%;
}

.Order-3 button {
    padding: 1.5rem 0rem;
    width: 65%;
    font-family: Overpass;
    font-style: normal;
    font-weight: 500;
    color: #11163A;
    cursor: pointer;
    font-size: 1.5rem;
    background: #FFFFFF;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.16);
    border-radius: 0.6rem;
    border: none;
}

.Order-4 {
    display: flex;
    max-width: 100%;
    width: 100%;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
}

.Order-4 p {
    font-family: Ubuntu;
    font-style: normal;
    font-weight: 500;
    font-size: 2rem;
    color: #F4F4F4;
}

.O4-content {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    max-width: 25%;
    width: 100%;
    background: #11163A;
    border-radius: 0.8rem;
}

.Order-5 {
    display: flex;
    max-width: 100%;
    width: 100%;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
}

.Order-5 p {
    font-family: Ubuntu;
    font-style: normal;
    font-weight: 500;
    font-size: 2rem;
    color: #F4F4F4;
}

.O5-content {
    display: flex;
    max-width: 25%;
    width: 100%;
}

.O5-content textarea {
    font-family: Ubuntu;
    font-style: normal;
    font-weight: normal;
    font-size: 1.4rem;
    color: #9B9B9B;
    background: #11163A;
    border-radius: 0.8rem;
    border: none;
    max-width: 100%;
    padding: 0.5rem;
}

.O5-content textarea:focus {
    outline: none;
}

.Order-6 {
    display: flex;
    max-width: 25%;
    width: 100%;
    margin-bottom: 5rem;
}

.Order-6 button {
    padding: 1.5rem 0rem;
    width: 60%;
    font-family: Overpass;
    font-style: normal;
    font-weight: 500;
    font-size: 1.6rem;
    color: #11163A;
    cursor: pointer;
    border: none;
    background: #FFFFFF;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.16);
    border-radius: 0.6rem;
}
.center {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 100%;
    width: 100%;
    height: 100vh;
}

.content {
    color: white;
    background-color: rgba(0, 0, 0, 0.3);
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    max-width: 100%;
    width: 100%;
    height: 100%;
}
.content {
    display: none;
}


