.Order-card-green {
    display: flex;
    padding: 1.5rem;
    justify-content: space-between;
    align-items: center;
    max-width: 100%;
    width: 100%;
    background: #11163A;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.1);
    border-radius: 0.8rem;
}

.OD-left {
    display: flex;
    max-width: 50%;
    width: 100%;
}

.OD-left-img {
    display: flex;
    max-width: 20%;
    width: 100%;
    padding: 1rem;
    justify-content: center;
    align-items: center;
}

.OD-left-img img {
    width: 100%;
    object-fit: cover;
}

.OD-left-content {
    display: flex;
    max-width: 80%;
    width: 100%;
    flex-direction: column;
    gap: 1rem;
    justify-content: center;
}

.OD-left-content p {
    font-family: Ubuntu;
    font-style: normal;
    font-weight: 500;
    font-size: 1.3rem;
    color: #F4F4F4;
}

.OD-left-content h5 {
    font-family: Ubuntu;
    font-style: normal;
    font-weight: 500;
    font-size: 1.6rem;
    color: #F4F4F4;
}

.OD-small {
    display: flex;
    align-items: center;
    max-width: 100%;
    width: 100%;
    gap: 1rem;
}

.OD-small img {
    width: 2rem;
}

.OD-right {
    display: flex;
    max-width: 50%;
    width: 100%;
    justify-content: flex-end;
    gap: 2rem;
}

.OD-ticketg {
    display: flex;
    flex-direction: row;
    max-width: 50%;
    width: 100%;
}

.blank3 {
    display: flex;
    max-width: 60%;
    width: 100%;
}

.boo3 {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    max-width: 40%;
    width: 100%;
    align-items: flex-start;
}

.ticket-btng {
    display: flex;
    align-items: center;
    gap: 0.6rem;
    justify-content: flex-start;
    max-width: 100%;
    width: 100%;
}

.ticket-btn-progressg {
    display: flex;
    width: 1.3rem;
    height: 1.3rem;
    background: #009A63;
    box-shadow: 0px 0px 4px rgba(0, 192, 124, 0.8);
    border-radius: 50%;
}

.OD-ticketg p {
    font-family: Ubuntu;
    font-style: normal;
    font-weight: 500;
    font-size: 1.4rem;
    color: #F4F4F4;
}

.OD-track {
    display: flex;
    flex-direction: column;
    max-width: 40%;
    width: 100%;
    align-items: center;
    justify-content: center;
}

.ticket-btng p {
    font-family: Ubuntu;
    font-style: normal;
    font-weight: normal;
    font-size: 1.4rem;
    color: #009A63;
}

.ticket-price5 {
    display: flex;
    max-width: 100%;
    width: 100%;
    padding: 0.25rem;
    background: linear-gradient(276.34deg, #11163A 0%, #3A4385 100%);
    box-shadow: 0px 0px 4px rgba(255, 255, 255, 0.16);
    border-radius: 3px;
    text-align: center;
}

.ticket-price5 p {
    font-family: Ubuntu;
    font-style: normal;
    font-weight: 500;
    font-size: 1.45rem;
    color: #F4F4F4;
}

.OD-track button {
    width: 70%;
    border: none;
    padding: 1.5rem;
    background: #FFFFFF;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.16);
    border-radius: 0.6rem;
    font-family: Overpass;
    font-style: normal;
    font-weight: 600;
    font-size: 1.5rem;
    color: #11163A;
    cursor: pointer;
}